.p-mains{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    // grid-row: auto auto auto;
    padding: 10px;
    margin: 20px;
    text-align:justify;
    justify-content: center;
    align-content: space-around;
    text-align: center;
    gap: 8px;
    
    

    .p-box{
        padding: 10px;
        border-radius: 10px;
        border: 1px solid grey;

        img{
            width: 200px;
            width: 200px;
        }
        
        .btn{
            button{
            height: 50px;
            width: 100px;
            border-radius: 5px;
            }
        }
    }

    
}