.main{
    height: 70px;
   
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
    border-bottom: 1px solid gray;

    .left{
        font-size: 70px;
        cursor: pointer;
        // text-align: center;
       font-size: larger;
       background-color: greenyellow;
       border-radius: 8px; 
       padding: 1px;
             

       
        
    
       

        .green{
            color: greenyellow;
            font-weight: bolder;
            font-style: italic;
            background-color: red;
            padding: 2px;
            border-radius: 5px;
            

        }
         .basket{
            color: greenyellow;
            font-weight: bolder;
            background-color: red;
            padding: 2px;
            border-radius: 5px;
            

        }
    }

    .center{
        display: flex;
        gap: 15px;
        list-style: none;
        
        
     li{
        cursor: pointer;
       
    }
    
    
    }

    a{
        text-decoration: none;
        color: black;
        font-size:25px;
        
    }

   

    .right{
        display: flex;
        align-items: center;
        font-size: 25px;
        cursor: pointer;
        gap: 10px;

        button{
            padding: 9px;
            margin: 5px;
            border-radius: 5px;
            cursor: pointer;
            transition-property: background-color;
            transition: 0.5s;
            background-color: rgba(66, 123, 230, 0.877);
  
        }
        button:hover{
            background-color:rgba(255, 255, 255, 0.897);

        }
        svg:hover{
            border-radius: 5px;
            background-color:grey;
        }
        
    }
    
    
    
}








